/* global Office */

// Simple storage utility for add-in settings
export class AddInStorage {
  static async saveApiKey(apiKey) {
    try {
      if (Office.context?.roamingSettings) {
        // Use Office roaming settings if available
        Office.context.roamingSettings.set('api_key', apiKey);
        await new Promise((resolve, reject) => {
          Office.context.roamingSettings.saveAsync((result) => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
              resolve();
            } else {
              reject(new Error('Failed to save API key'));
            }
          });
        });
      } else {
        // Fallback to localStorage
        localStorage.setItem('outlook_addin_api_key', apiKey);
      }
    } catch (error) {
      console.error('Error saving API key:', error);
      // Fallback to localStorage
      localStorage.setItem('outlook_addin_api_key', apiKey);
    }
  }

  static async getApiKey() {
    try {
      if (Office.context?.roamingSettings) {
        // Use Office roaming settings if available
        return Office.context.roamingSettings.get('api_key');
      } else {
        // Fallback to localStorage
        return localStorage.getItem('outlook_addin_api_key');
      }
    } catch (error) {
      console.error('Error getting API key:', error);
      // Fallback to localStorage
      return localStorage.getItem('outlook_addin_api_key');
    }
  }

  static async removeApiKey() {
    try {
      if (Office.context?.roamingSettings) {
        Office.context.roamingSettings.remove('api_key');
        await new Promise((resolve, reject) => {
          Office.context.roamingSettings.saveAsync((result) => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
              resolve();
            } else {
              reject(new Error('Failed to remove API key'));
            }
          });
        });
      } else {
        localStorage.removeItem('outlook_addin_api_key');
      }
    } catch (error) {
      console.error('Error removing API key:', error);
      localStorage.removeItem('outlook_addin_api_key');
    }
  }
}
