/* global process */

// Configuration for the Outlook add-in
export const CONFIG = {
  // API Configuration
  API_BASE_URL: 'https://api.hku.hk/openai/deployments/gpt-4.1/chat/completions?api-version=2025-01-01-preview',
  
  // This will be set by the user in the add-in interface
  // Never hardcode API keys in production!
  API_KEY: null,
  
  // Default settings
  DEFAULT_FONT: 'Calibri, Arial, sans-serif',
  DEFAULT_FONT_SIZE: '12pt',
  DEFAULT_LINE_HEIGHT: '1.15',
  DEFAULT_COLOR: '#000000'
};

// Function to set API key (will be called from UI)
export function setApiKey(apiKey) {
  CONFIG.API_KEY = apiKey;
}

// Function to get API key (with validation)
export function getApiKey() {
  if (!CONFIG.API_KEY) {
    throw new Error('API key not configured. Please set your API key in the add-in settings.');
  }
  return CONFIG.API_KEY;
}
