/* global Office console */

import { AddInStorage } from '../utils/storage.js';
import { CONFIG } from '../config/config.js';

export async function draftByAI() {
  try {
    // Get API key from storage
    const apiKey = await AddInStorage.getApiKey();
    if (!apiKey) {
      const loadingDiv = document.getElementById("loadingIcon");
      if (loadingDiv) {
        loadingDiv.innerHTML = `
          <div style="color: orange; padding: 10px;">
            ⚠️ API key not configured<br>
            <small>Please set your API key first:</small><br>
            <input type="password" id="apiKeyInput" placeholder="Enter your API key" style="width: 100%; margin: 5px 0;">
            <button onclick="saveApiKey()" style="width: 100%; margin: 5px 0; padding: 5px; background: #0078d4; color: white; border: none; border-radius: 3px;">Save API Key</button>
            <small>Your API key will be stored securely in your Office settings.</small>
          </div>
        `;
        loadingDiv.style.display = "block";
      }
      return;
    }
    // Wait for Office to be ready
    if (!Office.context || !Office.context.mailbox) {
      throw new Error("Office context not available. Please wait for Office to load.");
    }

    const item = Office.context.mailbox.item;
    if (!item) {
      throw new Error("No email item found. Please open an email first.");
    }

    // Log debugging information about the current context
    const itemType = item.itemType || "unknown";
    const itemClass = item.itemClass || "unknown";
    
    // Better compose mode detection
    const isComposeMode = itemType === Office.MailboxEnums.ItemType.Message && 
                         item.body && 
                         typeof item.body.setAsync === 'function';
    
    // Only allow this function to run in read mode (not in compose mode)
    if (isComposeMode) {
      const loadingDiv = document.getElementById("loadingIcon");
      if (loadingDiv) {
        loadingDiv.innerHTML = `
          <div style="color: orange; padding: 10px;">
            ⚠️ This function is only available in read mode<br>
            <small>Please use this add-in when reading emails, not when composing.</small>
          </div>
        `;
        loadingDiv.style.display = "block";
        
        // Hide message after 5 seconds
        setTimeout(() => {
          loadingDiv.style.display = "none";
        }, 5000);
      }
      return;
    }
    
    console.log("Office context info:", {
      itemType,
      itemClass,
      isComposeMode,
      hasDisplayReplyAllForm: !!item.displayReplyAllForm,
      hasDisplayReplyAllFormAsync: !!item.displayReplyAllFormAsync,
      hasBody: !!item.body,
      hasBodySetAsync: !!(item.body && item.body.setAsync),
      hasBodyGetAsync: !!(item.body && item.body.getAsync),
      officeVersion: Office.context.mailbox.diagnostics?.hostName,
      platform: Office.context.platform
    });

    // Show loading message in the UI
    const loadingDiv = document.getElementById("loadingIcon");
    if (loadingDiv) {
      loadingDiv.style.display = "block";
      loadingDiv.innerHTML = `
        <div style="margin-bottom: 5px;">
          📧 Processing ${itemType} item...<br>
          🔍 Reading conversation...
        </div>
      `;
    }

    // Get subject with error handling
    let subjectResult = "";
    try {
      if (item.subject && typeof item.subject.getAsync === 'function') {
        subjectResult = await new Promise((resolve, reject) => {
          item.subject.getAsync((asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
              resolve(asyncResult.value || "");
            } else {
              reject(asyncResult.error?.message || "Failed to get subject");
            }
          });
        });
      } else if (item.subject) {
        // Fallback: try direct property access
        subjectResult = item.subject || "";
      }
    } catch (subjectError) {
      console.log("Subject error:", subjectError);
      subjectResult = "[Subject not available]";
    }

    // Get body with error handling - different approach for compose vs read mode
    let bodyResult = "";
    try {
      if (item.body && typeof item.body.getAsync === 'function') {
        bodyResult = await new Promise((resolve, reject) => {
          // For compose mode, get HTML; for read mode, get text
          const coercionType = isComposeMode ? Office.CoercionType.Html : Office.CoercionType.Text;
          item.body.getAsync(coercionType, (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
              resolve(asyncResult.value || "");
            } else {
              reject(asyncResult.error?.message || "Failed to get body");
            }
          });
        });
      } else if (item.body) {
        // Fallback: try direct property access
        bodyResult = item.body || "";
      }
    } catch (bodyError) {
      console.log("Body error:", bodyError);
      bodyResult = "[Email body not available]";
    }

    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: blue;">
          🤖 Generating AI response...<br>
          ⏳ Please wait...
        </div>
      `;
    }

    const userInput = document.getElementById("aiInput")?.value || "";
    
    // Create the AI prompt - only for read mode now
    const body = {
      messages: [
        { 
          role: "system", 
          content: `You are an assistant that helps draft email replies. Your response should be a professional email draft with proper formatting. 
          
Guidelines:
- ALWAYS start with "Dear [Name]" or "Dear [Title]" as the greeting
- Use professional email formatting with line breaks between paragraphs
- Keep responses CONCISE and to the point unless user specifically requests detailed explanation
- Use my same tone in the email draft
- Do not include the original email content in your response
- Do NOT add email subject line
- Do NOT add signature, closing, or sign-off (no "Best regards", "Sincerely", etc.)
- End your response with the main content only
- When replying to technical inquiries, answer step by step with clear formatting
- Keep paragraphs concise and well-structured
- Focus only on the response content without any email formatting elements` 
        },
        { 
          role: "user", 
          content: `Email to reply to:
          
Subject: ${subjectResult}

Email Body: ${bodyResult}

User Instructions: ${userInput || "Generate an appropriate response to this email"}` 
        },
      ],
    };

    const response = await fetch(CONFIG.API_BASE_URL, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: {
        'Content-Type': 'application/json',
        'Cache-Control': 'no-cache',
        'api-key': apiKey,
      },
    });

    if (!response.ok) {
      throw new Error(`API request failed: ${response.status} ${response.statusText}`);
    }

    const responseData = await response.json();
    if (!responseData.choices || !responseData.choices[0] || !responseData.choices[0].message) {
      throw new Error("Invalid API response format");
    }
    
    const aiDraft = responseData.choices[0].message.content;

    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: blue;">
          📧 Opening reply window...
        </div>
      `;
    }

    // Since we're only in read mode, open Reply All with formatted content
    // Use Calibri 12pt formatting - force 12pt font size
    const emailParagraphs = aiDraft.split('\n').filter(line => line.trim().length > 0);
    const formattedDraft = emailParagraphs.map(paragraph => 
      `<div style="font-family: Calibri, Arial, sans-serif; font-size: 12pt !important; line-height: 1.15; color: #000000;">${paragraph.trim()}</div>`
    ).join('<br>');

    try {
      if (item.displayReplyAllFormAsync) {
        // Use the async version for better control
        await new Promise((resolve, reject) => {
          item.displayReplyAllFormAsync(formattedDraft, (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
              if (loadingDiv) {
                loadingDiv.innerHTML = `
                  <div style="color: green; font-weight: bold;">
                    ✅ Reply All window opened!<br>
                    📝 AI draft populated automatically<br>
                    👀 Check your Reply All window
                  </div>
                `;
              }
              resolve(asyncResult.value);
            } else {
              reject(new Error(asyncResult.error?.message || "Failed to open Reply All"));
            }
          });
        });
      } else if (item.displayReplyAllForm) {
        // Fallback to synchronous version
        item.displayReplyAllForm(formattedDraft);
        if (loadingDiv) {
          loadingDiv.innerHTML = `
            <div style="color: green; font-weight: bold;">
              ✅ Reply All window opened!<br>
              📝 AI draft populated automatically
            </div>
          `;
        }
      } else {
        throw new Error("Reply All functionality not available");
      }
    } catch (replyError) {
      console.error("Failed to open Reply All:", replyError);
      
      // Fallback: copy to clipboard and show instructions
      try {
        await navigator.clipboard.writeText(aiDraft);
        if (loadingDiv) {
          loadingDiv.innerHTML = `
            <div style="color: orange;">
              ⚠️ Auto Reply All failed<br>
              📋 AI draft copied to clipboard<br>
              👆 Please click "Reply All" manually and paste (Cmd+V)
            </div>
          `;
        }
      } catch (clipboardError) {
        // Last resort: show the text for manual copying
        if (loadingDiv) {
          loadingDiv.innerHTML = `
            <div style="color: red;">
              ❌ Auto Reply All failed<br>
              📝 Please copy this draft manually:<br>
              <textarea readonly style="width:100%; height:100px; margin-top:5px; font-size:12px;">${aiDraft}</textarea>
              <br><small>Then click "Reply All" in Outlook and paste it.</small>
            </div>
          `;
        }
      }
    }

    // Hide loading after 5 seconds
    setTimeout(() => {
      if (loadingDiv) {
        loadingDiv.style.display = "none";
      }
    }, 5000);

  } catch (error) {
    const loadingDiv = document.getElementById("loadingIcon");
    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: red; padding: 10px;">
          ❌ Error: ${error.message}<br>
          <small>Make sure you have an email open and try again.</small>
        </div>
      `;
      loadingDiv.style.display = "block";
      
      // Hide error after 8 seconds
      setTimeout(() => {
        loadingDiv.style.display = "none";
      }, 8000);
    }
  }
}

// Global function to save API key (called from the UI)
window.saveApiKey = async function() {
  const apiKeyInput = document.getElementById('apiKeyInput');
  const loadingDiv = document.getElementById("loadingIcon");
  
  if (!apiKeyInput || !apiKeyInput.value.trim()) {
    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: red; padding: 10px;">
          ❌ Please enter a valid API key
        </div>
      `;
      setTimeout(() => {
        loadingDiv.style.display = "none";
      }, 3000);
    }
    return;
  }

  try {
    await AddInStorage.saveApiKey(apiKeyInput.value.trim());
    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: green; padding: 10px;">
          ✅ API key saved successfully!<br>
          <small>You can now use the Draft by AI feature.</small>
        </div>
      `;
      setTimeout(() => {
        loadingDiv.style.display = "none";
      }, 3000);
    }
  } catch (error) {
    if (loadingDiv) {
      loadingDiv.innerHTML = `
        <div style="color: red; padding: 10px;">
          ❌ Failed to save API key: ${error.message}
        </div>
      `;
      setTimeout(() => {
        loadingDiv.style.display = "none";
      }, 5000);
    }
  }
};
